.pricing {
    width: 100%;
    padding: 6rem 1rem;
    background: #fff;
}
.card-container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
}
.card {
    border: 1px solid black;
    border-radius: 25px;
    color: black;
    text-align: center;
    padding: 1rem;
}
.card:hover {
    background-color: rgba(95, 66, 3, 0.9);
    opacity: 0.7;
}
.card h3 {
    color: black;
    font-size: 1.4rem;
    padding: 1rem;
}
.card p {
    color: black;
    padding: 10px 0;
}
.bar {
    border-bottom: 1px solid black;
    width: 10%;
    margin: 1.5rem auto;
    display: block;
}
.btc {
    font-size: 4rem;
    font-weight: 600;
    padding: 1rem 0;
}
.card .btn {
    display: block;
    width: 80%;
    margin: 2rem auto;
}

@media screen and (max-width: 360px) {
    .card-container {
        max-width: 360px;
        margin: auto;
        grid-template-columns: 1fr;
    }
    .card {
        max-width: 70%;
    }
    .btc {
        font-size: 2rem;
        font-weight: 600;
        padding: 1rem 0;
    }
}

@media screen and (max-width: 740px) and (min-width: 360px) {
    .card-container {
        max-width: 740px;
        margin: auto;
        grid-template-columns: 1fr;
    }
}